import ApolloClient, { gql } from 'apollo-boost';
import { navigate } from 'gatsby';
import fetch from 'isomorphic-fetch';
import * as loadingActions from './loadingActions';
import * as stationActions from './stationActions';
import { query } from '../utils/graphql';

const LOGIN = `
  query LOGIN($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      errorMsg
    }
  }
`;

export const login: Function = (email: string, password: string) => async (
  dispatch: Function,
  getState: Function
) => {
  dispatch(loadingActions.loadingStart());
  const data: any = await query(LOGIN, {
    email,
    password,
  });
  dispatch(loadingActions.loadingEnd());
  if (data.login && data.login.errorMsg === 'ok') {
    localStorage.setItem('sewage:token', data.login.token);
    navigate('/');
  } else {
    console.log('Invalid login');
  }
};

export const login2 = () => 'login';
